.Banner{
    color:white;
    object-fit: contain;
    height: 448px;
}
.Banner__contents{

margin-left: 30px;
padding-top: 140px;
height:140px;

}
.Banner__title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}
.banner__description{
    width:45rem;
    line-height: 1.3;
    padding-top:1rem;
    font-size:0.8rem;
    max-width: 360px;
    height:80px;
}
.bannner__Button{
    cursor: pointer;
    color:#fff;
    outline: none;
    border: none/* 1px solid white */;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left:2rem;
    padding-right:2rem;
    margin-right:1rem;
    padding-top:0.5rem;
    background-color: rgba(51,51,51,0.5);
    padding-bottom:0.5rem;  
}
.bannner__Button:hover{
    color:#000;
    background-color: #e6e6e6;
    transition:all 0.2s;
}
